/* Style for quick links section :: start */
.reign-option-box {
    background: #fff;
    border: 1px solid #ccc;
    width: 32%;
    min-height: 230px;
    max-height: 230px;
    margin: 0 .5% 1%;
    padding: 25px 25px 20px;
    overflow: hidden;
    box-sizing: border-box;
    text-align: center;
    position: relative;
}

.reign-option-box .option-wrapper .option-title {
    font-size: 17px;
    color: #333;
    margin: 0 0 15px;
    padding-bottom: 15px;
    text-transform: capitalize;
}

.reign-option-box .option-wrapper .option-desc {
    -webkit-box-flex: 1;
    -ms-flex: 1 0 auto;
    flex: 1 0 auto;
    display: block;
    margin: 0 0 20px;
    font-size: 14px;
    line-height: 1.6;
    font-weight: 400;
    color: #777;
}

.reign-option-box .option-wrapper .option-link-area {
    padding: 20px 0 0;
    border-top: 1px solid #eaeaea;
    margin: 0;
    position: absolute;
    left: 0;
    right: 0;
    bottom: 20px;
}

.reign-option-box .option-wrapper .option-link-area a.option-link {
    text-decoration: none;
    padding: 10px 30px;
    margin: 0;
    border-radius: 25px;
    border: 1px solid;
    display: inline-block
}

.reign-option-box {
    float: left;
}
.reign-option-boxes {
    display: inline-block;
}

@media(max-width: 1050px) {
    .reign-option-box {
        width: 49%;
    }
}

@media(max-width: 570px) {
    .reign-option-box {
        width: 100%;
    }
}
/* Style for quick links section :: end */

/* Style the reign-tab */
.nav-tab-wrapper, .wrap h2.nav-tab-wrapper, h1.nav-tab-wrapper
.nav-tab:focus {
    margin-bottom: 20px;
}

.nav-tab:focus, .nav-tab:hover {
    border: none;
    background: transparent;
    box-shadow: none;
}

.nav-tab {
    float: left;
    border: none;
    border-bottom: none;
    margin-left: 0;
    padding: 12px 20px;
    font-size: 15px;
    line-height: 24px;
    background: transparent;
    border-bottom: 2px solid transparent;
}

.nav-tab:hover {
    border-bottom: 2px solid #555;
}

.nav-tab-active, .nav-tab-active:focus, .nav-tab-active:focus:active, .nav-tab-active:hover {
    border-bottom: 2px solid #2872aa;
    background: transparent;
    color: #2872aa;
}

div.reign-tab {
    float: left;
    background-color: #f7f8fa;
    width: 20%;
    border-right: 0;
}

button.reign-tablinks {
    position: relative;
}

button.reign-tablinks:before {
    content: ' ';
    font-family: 'rg-icon';
    font-size: 16px;
    margin: 0 10px;
    text-align: center;
}

button.reign-tablinks.reign_pages:before {
    content: '\e887';
}

button.reign-tablinks.custom_code:before {
    content: '\e822';
}

button.reign-tablinks.wp_login_screen:before {
    content: '\e865';
}

button.reign-tablinks.avatar_settings:before {
    content: '\e8d1';
}

button.reign-tablinks.advanced_settings:before {
    content: '\e8da';
}

button.reign-tablinks.group_cover_image:before {
    content: '\e855';
}

button.reign-tablinks.xprofile_cover_image:before {
    content: '\e841';
}

/* Style the buttons inside the reign-tab */
div.reign-tab button {
    font-size: 14px;
    display: block;
    background-color: inherit;
    color: #5f5f5f;
    padding: 24px 10px;
    width: 100%;
    border: none;
    border-bottom: 1px solid #eee;
    border-radius: 0;
    outline: none;
    text-align: left;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
}

/* Change background color of buttons on hover */
div.reign-tab button:hover {
    background-color: #fff;
    color: #2872aa;
}

/* Create an active/current "reign-tab button" class */
div.reign-tab button.active {
    background-color: #fff;
    position: relative;
    color: #2872aa;
}

/* Style the reign-tab content */
.reign-tabcontent {
    width: 68%;
    height: auto;
    min-height: 300px;
    background: #fff;
    float: left;
    padding: 15px 30px;
}

.form-table label {
    font-size: 16px;
    cursor: pointer;
    font-weight: 500;
}

.form-table th, .form-wrap label {
    color: #444;
}

span.option-desc {
    display: block;
    margin-top: 15px;
    font-size: 13px;
    line-height: 1.5;
    font-style: italic;
}

.form-table tr:not(:first-child) {
    border-top: 1px solid #eee;
}

.form-table th {
    width: 290px;
    font-weight: 400;
}

.form-table td {
    width: 290px;
    margin: 20px 0;
}

img.reign_default_cover_image {
    width: 400px !important;
    height: 200px !important;
    object-fit: cover;
}

.wp-admin select {
    margin: 0;
    margin-right: 5px;
}

a.reign-remove-file-button {
    display: block;
    margin: 10px 0;
}

.wp-core-ui p .button {
    vertical-align: baseline;
    margin-bottom: 8px;
}

/* support accordion */
.ui-accordion-header {
    background: #ddd;
    padding: 20px 10px;
    margin: 0;
    margin-bottom: 5px;
    position: relative;
}

.ui-accordion-header:after {
    content: '\e800';
    font-family: "rg-icon";
    font-style: normal;
    font-weight: normal;
    float: right;
    padding-right: 10px;
    cursor: pointer;
}

.ui-accordion-header.ui-state-active:after {
    content: '\e86e';
}

.ui-accordion-header:focus {
    border: none;
    outline: none;
}

.ui-accordion-content {
    background: #fff;
    padding: 30px;
    margin: 0;
}

.ui-accordion-content p {
    font-size: 14px;
}

@media screen and (max-width: 782px) {
    #wpbody .form-table td select {
        font-size: 14px;
        max-width: 185px;
        height: 36px;
    }

    .form-table th {
        padding-top: 10px;
        padding-bottom: 5px;
        border-bottom: 0;
    }

    .form-table label {
        display: block;
        margin: 10px 0;
    }
}

@media screen and (max-width: 670px) {
    .tooltip {
        margin-left: 50px;
    }

    div.reign-tab {
        width: 100%;
        height: auto;
        border-right: 1px solid #ccc;
        border-bottom: 0;
    }

    .reign-tabcontent {
        width: -webkit-calc(100% - 45px);
        width: calc(100% - 45px);
        padding-left: 15px;
    }

    div.reign-tab button.active:after {
        display: none;
    }
}

/* tooltip css */
.tooltip {
    position: relative;
    font-size: 11px;
    color: #fff;
    line-height: 16px;
    width: 16px;
    height: 16px;
    display: inline-block;
    background: #202e5b;
    border-radius: 100%;
    text-align: center;
    cursor: help;
}

.tooltip .tooltiptext {
    visibility: hidden;
    width: 120px;
    background-color: black;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 10px;
    position: absolute;
    z-index: 1;
    bottom: 150%;
    left: 50%;
    margin-left: -70px;
}

.tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: black transparent transparent transparent;
}

.tooltip:hover .tooltiptext {
    visibility: visible;
}

/*fonts*/
@font-face {
    font-family: 'rg-icon';
    src: url('../icons/rg-icon.eot?19043231');
    src: url('../icons/rg-icon.eot?19043231#iefix') format('embedded-opentype'),
        url('../icons/rg-icon.woff2?19043231') format('woff2'),
        url('../icons/rg-icon.woff?19043231') format('woff'),
        url('../icons/rg-icon.ttf?19043231') format('truetype'),
        url('../icons/rg-icon.svg?19043231#rg-icon') format('svg');
    font-weight: normal;
    font-style: normal;
}

/* loading section in reign theme settings */
.reign-animation-container {
    width: 100%;
    margin: auto;
    font-weight: 900;
    text-transform: uppercase;
    text-align: center;
    padding: 0 0 200px;
}
.reign-animate {
    font-size: 50px;
    margin: 100px 0 0;
}
.reign-animate span {
    display: inline-block;
}
.reign-animate span:nth-of-type(2) {
    animation-delay: .05s;
}
.reign-animate span:nth-of-type(3) {
    animation-delay: .1s;
}
.reign-animate span:nth-of-type(4) {
    animation-delay: .15s;
}
.reign-animate span:nth-of-type(5) {
    animation-delay: .2s;
}
/* Animation Seven */
.seven span {
    color: #348c04;
    opacity: 0;
    transform: translate(-150px, 0) scale(.3);
    animation: leftRight .5s forwards;
}
@keyframes leftRight {
    40% {
        transform: translate(50px, 0) scale(.7);
        opacity: 1;
        color: #348c04;
    }
    60% {
        color: #0f40ba;
    }
    80% {
        transform: translate(0) scale(2);
        opacity: 0;
    }
    100% {
        transform: translate(0) scale(1);
        opacity: 1;
    }
}
